import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import { Tabs , Tab }                               from 'material-ui/Tabs';
import TextField                                    from 'material-ui/TextField';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Paper                                        from 'material-ui/Paper';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import SwipeableViews                               from 'react-swipeable-views';





/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../../_abstract/c.abstract';







import '../s.support.customer.detail.css';







export default class ComponentSupportCustomerDetailPayment extends ComponentAbstract {



    static propTypes = {
        form        : PropTypes.object

        , data      : PropTypes.object

        , result    : PropTypes.object
    }


    static defaultProps = {
        form        : { }

        , data      : {  }

        , result    : { }
    }





    constructor( props ) {
        super( props )

        console.log( 'ComponentSupportCustomerDetailPayment : constructor : *** : [' + this.uuid() + '] ' , props );

    }
















    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {
        return (
            <div className={ ' support-customer-detail-padding-10 ' }>

                <Table
                    selectable={ false }
                    fixedHeader={ true }
                    style={ { width: "100%" } } tableLayout="auto">

                    <TableHeader
                        adjustForCheckbox={ false }
                        displaySelectAll={ false }>

                        <TableRow>

                            <TableHeaderColumn> Date </TableHeaderColumn>
                            <TableHeaderColumn> Payment Type </TableHeaderColumn>
                            <TableHeaderColumn> Status </TableHeaderColumn>

                        </TableRow>

                    </TableHeader>

                    <TableBody displayRowCheckbox={ false }>

                    </TableBody>

                </Table>

            </div>
        )
    }
}



