import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import { Tabs , Tab }                               from 'material-ui/Tabs';
import TextField                                    from 'material-ui/TextField';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Paper                                        from 'material-ui/Paper';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import SwipeableViews                               from 'react-swipeable-views';





/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../../_abstract/c.abstract';







import '../s.support.customer.detail.css';







export default class ComponentSupportCustomerDetailProfile extends ComponentAbstract {



    static propTypes = {
        form        : PropTypes.object

        , data      : PropTypes.object

        , result    : PropTypes.object
    }


    static defaultProps = {
        form        : { }

        , data      : {  }

        , result    : { }
    }





    constructor( props ) {
        super( props )

        console.log( 'ComponentSupportCustomerDetailProfile : constructor : *** : [' + this.uuid() + '] ' , props );

    }
















    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {
        return (
            <div className={ ' support-customer-detail-padding-10 ' }>

                <Grid fluid>

                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Manage Mode
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.profile.transactionManagement }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Corporate Online Limit
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.profile.transactionLimit }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Transaction Workflow
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.profile.transactionWorkflow }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Primary Account Number
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.profile.accNum }
                        </Col>
                    </Row>

                </Grid>

            </div>
        )
    }
}



