import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import { Tabs , Tab }                               from 'material-ui/Tabs';
import TextField                                    from 'material-ui/TextField';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Paper                                        from 'material-ui/Paper';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import SwipeableViews                               from 'react-swipeable-views';





/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../../_abstract/c.abstract';





/* ################################################### */
/*          helper         */
/* ################################################### */

import { ObjectUtil }                               from '../../../../../_util/util.object';







import '../s.support.customer.detail.css';







export default class ComponentSupportCustomerDetailAccount extends ComponentAbstract {


    static propTypes = {
        form        : PropTypes.object

        , data      : PropTypes.object

        , result    : PropTypes.object
    }


    static defaultProps = {
        form        : { }

        , data      : {  }

        , result    : { }
    }





    constructor( props ) {
        super( props )

        console.log( 'ComponentSupportCustomerDetailAccount : constructor : *** : [' + this.uuid() + '] ' , props );

    }









    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {

        return (
            <div className={ ' support-customer-detail-padding-10 ' }>

                <Grid fluid>
                    <Row className="show-grid">
                        <Col    md={ 4 }
                                className={ ' support-customer-detail-textAlign-center ' } >
                            Current Account
                        </Col>

                        <Col    md={ 4 }
                                className={ ' support-customer-detail-textAlign-center ' } >
                            Term Deposits
                        </Col>

                        <Col    md={ 4 }
                                className={ ' support-customer-detail-textAlign-center ' } >
                            Financing Accounts
                        </Col>
                    </Row>

                    <Row>
                        <Col md={ 4 }>

                            <Table
                                selectable={ false }
                                fixedHeader={ true }
                                style={ { width: "100%" } } tableLayout="auto">

                                <TableHeader
                                    adjustForCheckbox={ false }
                                    displaySelectAll={ false }>

                                    <TableRow>

                                        <TableHeaderColumn className={ ' support-customer-detail-textAlign-center ' }> Acc No </TableHeaderColumn>
                                        <TableHeaderColumn className={ ' support-customer-detail-textAlign-center ' }> Acc Name </TableHeaderColumn>

                                    </TableRow>

                                </TableHeader>

                                <TableBody displayRowCheckbox={ false }>
                                    {
                                        ObjectUtil.hasProperty ( this.props.result.account , 'currentAccounts' )
                                        ?
                                            this.props.result.account.currentAccounts.map( ( item , index ) => {
                                                return (
                                                    <TableRow
                                                                key={ '_support_customer_detail_account_current_' + index }>

                                                        <TableRowColumn> { item.acctNo } </TableRowColumn>
                                                        <TableRowColumn> { item.acctShortName } </TableRowColumn>

                                                    </ TableRow>
                                                );
                                            } )
                                        :
                                            ( null )
                                    }
                                </TableBody>

                            </Table>

                        </Col>

                        <Col md={ 4 }>

                            <div className={ ' support-customer-detail-padding-5 ' }>

                                <Table
                                    selectable={ false }
                                    fixedHeader={ true }
                                    style={ { width: "100%" } } tableLayout="auto">

                                    <TableHeader
                                        adjustForCheckbox={ false }
                                        displaySelectAll={ false }>

                                        <TableRow>

                                            <TableHeaderColumn className={ ' support-customer-detail-textAlign-center ' }> Acc No </TableHeaderColumn>
                                            <TableHeaderColumn className={ ' support-customer-detail-textAlign-center ' }> Acc Name </TableHeaderColumn>

                                        </TableRow>

                                    </TableHeader>

                                    <TableBody displayRowCheckbox={ false }>
                                        {
                                            ObjectUtil.hasProperty ( this.props.result.account , 'fixedDepositAccounts' )
                                            ?
                                                this.props.result.account.fixedDepositAccounts.map( ( item , index ) => {
                                                    return (
                                                        <TableRow
                                                                    key={ '_support_customer_detail_account_fixed' + index }>

                                                            <TableRowColumn> { item.acctNo } </TableRowColumn>
                                                            <TableRowColumn> { item.acctShortName } </TableRowColumn>

                                                        </ TableRow>
                                                    );
                                                } )
                                            :
                                                ( null )
                                        }
                                    </TableBody>

                                </Table>

                            </div>

                        </Col>

                        <Col md={ 4 }>

                            <div className={ ' support-customer-detail-padding-5 ' }>

                                <Table
                                    selectable={ false }
                                    fixedHeader={ true }
                                    style={ { width: "100%" } } tableLayout="auto">

                                    <TableHeader
                                        adjustForCheckbox={ false }
                                        displaySelectAll={ false }>

                                        <TableRow>
                                            <TableHeaderColumn className={ ' support-customer-detail-textAlign-center ' }> Acc No </TableHeaderColumn>
                                            <TableHeaderColumn className={ ' support-customer-detail-textAlign-center ' }> Acc Name </TableHeaderColumn>
                                        </TableRow>

                                    </TableHeader>

                                    <TableBody displayRowCheckbox={ false }>
                                        {
                                            ObjectUtil.hasProperty ( this.props.result.account , 'loanAccounts' )
                                            ?
                                                this.props.result.account.loanAccounts.map( ( item , index ) => {
                                                    return (
                                                        <TableRow
                                                                    key={ '_support_customer_detail_account_loan_' + index }>

                                                            <TableRowColumn> { item.acctNo } </TableRowColumn>
                                                            <TableRowColumn> { item.acctShortName } </TableRowColumn>

                                                        </ TableRow>
                                                    );
                                                } )
                                            :
                                                ( null )
                                        }
                                    </TableBody>

                                </Table>

                            </div>

                        </Col>
                    </Row>
                </Grid>


            </div>
        )
    }
}



