import React                                        from 'react';
import PropTypes                                    from 'prop-types';







/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import { Tabs , Tab }                               from 'material-ui/Tabs';
import TextField                                    from 'material-ui/TextField';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Paper                                        from 'material-ui/Paper';
import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import SwipeableViews                               from 'react-swipeable-views';





/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../../../_abstract/c.abstract';








/* ################################################### */
/*          helper         */
/* ################################################### */

import { ObjectUtil }                               from '../../../../../_util/util.object';










import '../s.support.customer.detail.css';







export default class ComponentSupportCustomerDetailInfo extends ComponentAbstract {




    static propTypes = {
        form        : PropTypes.object

        , data      : PropTypes.object

        , result    : PropTypes.object
    }


    static defaultProps = {
        form        : { }

        , data      : {  }

        , result    : { }
    }




    constructor( props ) {
        super( props )

        console.log( 'ComponentSupportCustomerDetailInfo : constructor : *** : [' + this.uuid() + '] ' , props );

    }
















    /*
        ################################################################################################################################################
        ################################################################################################################################################
                render
        ################################################################################################################################################
        ################################################################################################################################################
    */

    render() {

        return (
            <div className={ ' support-customer-detail-padding-10 ' }>

                <Grid fluid>

                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Business Name
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.bussName }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Business Registration Number
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.bussRegNumber }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Service Package
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.spCode }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Billing Code
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.billingCode }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Status
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.status }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Creation Date
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.creationDate }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Termination Date
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.terminationDate }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            RM Lead Code
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.rmLeadCode }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            RM Code
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.rmCode }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Home Branch
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.homeBranch }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            SME Type
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.smeType }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Resident Status
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.residentStatus }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Citizenship
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.citizenship }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Segment Code
                        </Col>

                        <Col md={ 10 } >
                            { this.props.result.info.segmentCode }
                        </Col>
                    </Row>

                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Address
                        </Col>

                        <Col md={ 10 } >
                            {
                                ObjectUtil.hasProperty( this.props.result.info , 'addresses' )
                                ?
                                    this.props.result.info.addresses.map(
                                        ( itemAddress , indexAddress ) => {
                                            return (
                                                <div key={ '_support_customer_detail_info_address_' + indexAddress }>

                                                    <Grid fluid>
                                                        <Row className="show-grid">
                                                            <Col md={ 2 } >
                                                                Type
                                                            </Col>
                                                            <Col md={ 10 } >
                                                                { itemAddress.type }
                                                            </Col>
                                                        </Row>

                                                        <Row className="show-grid">
                                                            <Col md={ 2 } >
                                                                Address
                                                            </Col>
                                                            <Col md={ 10 } >
                                                                { itemAddress.address1 } <br />
                                                                { itemAddress.address2 } <br />
                                                                { itemAddress.address3 }
                                                            </Col>
                                                        </Row>

                                                        <Row className="show-grid">
                                                            <Col md={ 2 } >
                                                                City
                                                            </Col>
                                                            <Col md={ 10 } > { itemAddress.city } </Col>
                                                        </Row>

                                                        <Row className="show-grid">
                                                            <Col md={ 2 } >
                                                                PostCode
                                                            </Col>
                                                            <Col md={ 10 } > { itemAddress.postcode } </Col>
                                                        </Row>

                                                        <Row className="show-grid">
                                                            <Col md={ 2 } >
                                                                State Code
                                                            </Col>
                                                            <Col md={ 10 } > { itemAddress.statecode } </Col>
                                                        </Row>
                                                    </Grid>

                                                </div>
                                            );
                                        }
                                    )
                                :
                                    ( null )
                            }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Contact
                        </Col>

                        <Col md={ 10 } >
                            {
                                ObjectUtil.hasProperty( this.props.result.info , 'addresses' )
                                ?
                                    this.props.result.info.contactInfo.map(
                                        ( itemContact , indexContact ) => {
                                            return (
                                                <div key={ '_support_customer_detail_info_contact_' + indexContact }>

                                                    <Grid fluid>
                                                        <Row className="show-grid">
                                                            <Col md={ 2 } >
                                                                Type
                                                            </Col>
                                                            <Col md={ 10 } >
                                                                { itemContact.type }
                                                            </Col>
                                                        </Row>

                                                        <Row className="show-grid">
                                                            <Col md={ 2 } >
                                                                Number
                                                            </Col>
                                                            <Col md={ 10 } >
                                                                { itemContact.number }
                                                            </Col>
                                                        </Row>
                                                    </Grid>

                                                </div>
                                            );
                                        }
                                    )
                                :
                                    ( null )
                            }
                        </Col>
                    </Row>


                    <Row className="show-grid" className={ ' support-customer-detail-paddingTop-15 support-customer-detail-paddingBottom-15 ' } >
                        <Col md={ 2 } >
                            Email
                        </Col>

                        <Col md={ 10 } >
                            {
                                ObjectUtil.hasProperty( this.props.result.info , 'addresses' )
                                ?
                                    this.props.result.info.emails.map(
                                        ( itemEmail , indexEmail ) => {
                                            return (
                                                <div key={ '_support_customer_detail_info_email_' + itemEmail }>

                                                    <Grid fluid>
                                                        <Row className="show-grid">
                                                            <Col md={ 2 } >
                                                                Type
                                                            </Col>
                                                            <Col md={ 10 } >
                                                                { itemEmail.type }
                                                            </Col>
                                                        </Row>

                                                        <Row className="show-grid">
                                                            <Col md={ 2 } >
                                                                Email
                                                            </Col>
                                                            <Col md={ 10 } >
                                                                { itemEmail.emailAddress }
                                                            </Col>
                                                        </Row>
                                                    </Grid>

                                                </div>
                                            );
                                        }
                                    )
                                :
                                    ( null )
                            }
                        </Col>
                    </Row>


                </Grid>


            </div>
        )
    }
}



